import axios from 'axios';
import { supabase } from '../config/supabase.config';

// Configuraci贸n base de axios
const API_URL = process.env.REACT_APP_API_URL || 'http://localhost:5000/api';

// Crear instancia de axios con configuraci贸n base
const api = axios.create({
  baseURL: API_URL,
  headers: {
    'Content-Type': 'application/json'
  }
});

// Interceptor para a帽adir el token de autenticaci贸n a las solicitudes
api.interceptors.request.use(
  async (config) => {
    // Obtener la sesi贸n actual de Supabase
    const { data } = await supabase.auth.getSession();
    const session = data.session;
    
    if (session) {
      config.headers['Authorization'] = `Bearer ${session.access_token}`;
    }
    return config;
  },
  (error) => {
    return Promise.reject(error);
  }
);

// Interceptor para manejar errores de respuesta
api.interceptors.response.use(
  (response) => {
    return response;
  },
  (error) => {
    // Manejar errores de autenticaci贸n (401)
    if (error.response && error.response.status === 401) {
      // Cerrar sesi贸n en Supabase
      supabase.auth.signOut();
      window.location.href = '/login';
    }
    return Promise.reject(error);
  }
);

export default api;